import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-employee',
  templateUrl: './employee.component.html',
  styleUrls: ['./employee.component.scss']
})
export class EmployeeComponent implements OnInit {

  constructor(
    private vhAuth: VhAuth,
    private lang: LanguageService,
    private vhComponent: VhComponent,
    private router: Router
  ) {

  }
  ngOnInit() {

  }
  gotoListEmployee() {
    if (this.vhAuth.checkMyPermission("management_enable_employee")) this.router.navigate(['/cafe/dashboard/management/partner/employee/employee']);
    else this.vhComponent.showToast(1500, this.lang.translate('You do not have this rights'), "alert-toast")
  }
  gotoWorkgroup() {
    this.router.navigate(['/cafe/dashboard/management/partner/employee/workgroup'])
  }
  gotoManualTimekeeping() {

  }
  gotoTimeAttendanceMachine() {

  }
  gotoPaybill() {
    this.router.navigate(['/cafe/dashboard/management/partner/employee/paybill'])
  }
  gotoPayoff() {
    this.router.navigate(['/cafe/dashboard/management/partner/employee/payoff'])
  }
  gotoTimekeepingHistory() {
    this.router.navigate(['/cafe/dashboard/management/partner/employee/history'])
  }
  goBack() {
    this.router.navigate(['/cafe/dashboard/management/partner']);
  }
}
