import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { EmployeeComponent } from './employee.component';
import { NgZorroAntModule } from '../../../../ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
const routes: Routes = [
  {
    path: '',
    loadChildren: () => import('./list-employee/list-employee.module').then(m => m.ListEmployeeModule)
  },
  {
    path: 'employee',
    loadChildren: () => import('./list-employee/list-employee.module').then(m => m.ListEmployeeModule)
  },
  {
    path: 'workgroup',
    loadChildren: () => import('./workgroup/workgroup.module').then(m => m.WorkgroupModule)
  },
  {
    path: 'paybill',
    loadChildren: () => import('./paybill/paybill.module').then(m => m.PaybillModule)
  },
  {
    path: 'payoff',
    loadChildren: () => import('./pay-off/pay-off.module').then(m => m.PayOffModule)
  },
  {
    path: 'history',
    loadChildren: () => import('./timekeeping-history/timekeeping-history.module').then(m => m.TimekeepingHistoryModule)
  }
]

@NgModule({
  declarations: [EmployeeComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    FormsModule,
    ReactiveFormsModule.withConfig({ warnOnNgModelWithFormControl: 'never' }),
    TranslateModule
  ]
})
export class EmployeeModule { }
